html{
    box-sizing: border-box;
  }
  
 *,
 *::after,
 *::before{
   box-sizing: inherit;
 }
 *{
   margin: 0px;
   padding: 0px;
 }
 
 ul[class],
 ol[class] {
   padding: 0;
 }
 
 body,
 h1,
 h2,
 h3,
 h4,
 h5,
 h6,
 p,
 ul,
 ol,
 li,
 figure,
 figcaption,
 blockquote,
 dl,
 dd {
   margin: 0;
   font-size: inherit;
 }
 
 ul[class] {
   list-style: none;
 }
 
 img {
   max-width: 100%;
   display: block;
 }
 
 input,
 button,
 textarea,
 select {
   font: inherit;
 }
 
 a{
   text-decoration: none;
 }
 li{
   list-style: none;
 }
 
 